import { useState } from '@wordpress/element';
import { useBlockProps, InspectorControls, RichText, MediaUpload, MediaUploadCheck } from '@wordpress/block-editor';
import { Button, ToggleControl, Flex, FlexBlock } from '@wordpress/components';
import { __ } from '@wordpress/i18n';

import blockImage from '../../../../admin/assets/img/blocks/block-31.jpg';

import VideoHelpPanel from './controls/VideoHelpPanel';
import ContentPanel from './controls/ContentPanel';
import BgAnchorPanel from './controls/BgAnchorPanel';

const Edit = ({ attributes, setAttributes }) => {
  const { teacherName, teacherPosition, teacherDescr, teacherImageId, teacherImageData } = attributes;

  const [isPreview, setIsPreview] = useState(false);

  const togglePreview = () => {
    setIsPreview(!isPreview);
  };

  const teachers = JSON.parse(attributes.teachers || '[]');

  const updateTeachers = (newTeachers) => {
    setAttributes({
      teachers: JSON.stringify(newTeachers)
    });
  };

  // Handler - teacher
  const onSelectImage = (media) => {
    setAttributes({
      teacherImageId: media.id,
      teacherImageData: {
        url: media.url,
        alt: media.alt || '',
        responsive: media.responsive || {
          webp: '',
          jpg: '',
          default: media.url,
        }
      }
    });
  };

  const onRemoveImage = () => {
    setAttributes({
      teacherImageId: 0,
      teacherImageData: {
        url: '',
        alt: '',
        responsive: {
          webp: '',
          jpg: '',
          default: '',
        }
      }
    });
  };

  // teachers block props
  const addTeacherBlock = () => {
    updateTeachers([
      ...teachers,
      { title: '', items: [] }
    ]);
  };

  const removeTeacherBlock = (blockIndex) => {
    const newTeachers = [...teachers];
    newTeachers.splice(blockIndex, 1);
    updateTeachers(newTeachers);
  };

  const addTeacher = (blockIndex) => {
    const newTeachers = teachers.map((block, idx) =>
      idx !== blockIndex
        ? block
        : {
          ...block,
          items: [
            ...block.items,
            {
              name: '',
              role: '',
              descr: '',
              imageId: 0,
              imageData: {},
            },
          ],
        }
    );

    updateTeachers(newTeachers);
  };

  const removeTeacher = (blockIndex, index) => {
    const newTeachers = teachers.map((block, bIdx) =>
      bIdx !== blockIndex
        ? block
        : {
          ...block,
          items: block.items.filter((_, i) => i !== index),
        }
    );

    updateTeachers(newTeachers);
  };

  const moveTeacher = (blockIndex, index, direction) => {
    const items = [...teachers[blockIndex].items];
    const newIndex = index + direction;

    if (newIndex < 0 || newIndex >= items.length) return;

    [items[index], items[newIndex]] = [items[newIndex], items[index]];

    const newTeachers = [...teachers];
    newTeachers[blockIndex].items = items;
    updateTeachers(newTeachers);
  };

  const updateBlockTitle = (blockIndex, value) => {
    const newTeachers = teachers.map((block, idx) =>
      idx !== blockIndex ? block : { ...block, title: value }
    );

    updateTeachers(newTeachers);
  };

  const updateTeacherField = (blockIndex, index, field, value) => {
    const newTeachers = teachers.map((block, bIdx) =>
      bIdx !== blockIndex
        ? block
        : {
          ...block,
          items: block.items.map((item, iIdx) =>
            iIdx !== index ? item : { ...item, [field]: value }
          ),
        }
    );

    updateTeachers(newTeachers);
  };

  const updateTeacherImage = (blockIndex, index, media) => {
    const newTeachers = teachers.map((block, bIdx) =>
      bIdx !== blockIndex
        ? block
        : {
          ...block,
          items: block.items.map((item, iIdx) =>
            iIdx !== index
              ? item
              : {
                ...item,
                imageId: media.id,
                imageData: {
                  url: media.url,
                  alt: media.alt || '',
                },
              }
          ),
        }
    );

    updateTeachers(newTeachers);
  };



  const blockProps = useBlockProps({
    className: 'block-style mgu-advantages'
  });

  return (
    <>
      <InspectorControls>
        <VideoHelpPanel />
        <ContentPanel attributes={attributes} setAttributes={setAttributes} />
        <BgAnchorPanel attributes={attributes} setAttributes={setAttributes} />
      </InspectorControls>

      <div {...blockProps}>
        <div className="advanced-block">
          <div className="block-info">
            <span className="block-info-title"> Block 31 -  ()</span>
            <ToggleControl
              label={isPreview ? __(' ', 'theme') : __(' ', 'theme')}
              checked={isPreview}
              onChange={togglePreview}
            />
          </div>

          {!isPreview && (
            <img src={blockImage} alt="MGUBS" style={{ width: '100%', height: 'inherit', objectFit: 'contain' }} />
          )}

          {isPreview && (
            <div className="advanced-block-content" style={{ alignItems: "flex-start", alignContent: 'flex-start' }}>

              <h3 style={{ width: '100%', margin: '0' }}>:</h3>
              <div style={{ width: '100%', padding: '12px', border: '2px dashed rgba(0,124,186,.25)', borderRadius: '4px' }}>
                <Flex align="flex-start">
                  <FlexBlock>
                    <MediaUploadCheck>
                      <MediaUpload
                        onSelect={onSelectImage}
                        allowedTypes={['image']}
                        value={teacherImageId}
                        render={({ open }) => (
                          <div className="advanced-block-image advanced-block-image-100">
                            <div className="label-image"></div>
                            {teacherImageData.url ? (
                              <>
                                <img
                                  src={teacherImageData.url}
                                  className="advanced-image-preview"
                                  alt=""
                                  style={{ borderRadius: '8px' }}
                                />
                                <div style={{ display: 'flex', gap: '6px', marginTop: '4px' }}>
                                  <Button onClick={open} variant="secondary" size="small">
                                     {__('', 'theme')}
                                  </Button>
                                  <Button
                                    onClick={onRemoveImage}
                                    variant="tertiary"
                                    size="small"
                                    isDestructive
                                  >
                                     {__('', 'theme')}
                                  </Button>
                                </div>
                              </>
                            ) : (
                              <Button onClick={open} variant="primary">
                                 {__(' ', 'theme')}
                              </Button>
                            )}
                          </div>
                        )}
                      />
                    </MediaUploadCheck>
                  </FlexBlock>
                  <FlexBlock>
                    <>
                      <label className="my-rich-text__label"></label>
                      <RichText
                        tagName="div"
                        label=""
                        value={teacherName}
                        onChange={(value) => setAttributes({ teacherName: value })}
                        placeholder={__('...', 'theme')}
                        allowedFormats={[]}
                      />
                    </>
                    <>
                      <label className="my-rich-text__label"></label>
                      <RichText
                        tagName="div"
                        label=""
                        value={teacherPosition}
                        onChange={(value) => setAttributes({ teacherPosition: value })}
                        placeholder={__('...', 'theme')}
                        allowedFormats={[]}
                      />
                    </>
                    <>
                      <label className="my-rich-text__label"></label>
                      <RichText
                        tagName="div"
                        label=""
                        value={teacherDescr}
                        onChange={(value) => setAttributes({ teacherDescr: value })}
                        placeholder={__('...', 'theme')}
                        allowedFormats={[]}
                      />
                    </>
                  </FlexBlock>
                </Flex>
              </div>

              <div style={{ display: 'block', width: '100%', height: '2px' }}></div>

              <h3 style={{ width: '100%', margin: '0' }}>:</h3>
              {teachers.map((block, blockIndex) => (
                <div
                  key={blockIndex}
                  style={{
                    display: 'block',
                    width: '100%',
                    marginBottom: 16,
                    padding: 12,
                    border: '2px solid rgba(0,124,186,.3)',
                    borderRadius: 6
                  }}
                >
                  <Flex align="center">
                    <FlexBlock>
                      <label className="my-rich-text__label"> </label>
                      <RichText
                        tagName="div"
                        value={block.title}
                        onChange={(value) => updateBlockTitle(blockIndex, value)}
                        placeholder=":  "
                        allowedFormats={[]}
                      />
                    </FlexBlock>

                    <Button
                      isDestructive
                      variant="tertiary"
                      onClick={() => removeTeacherBlock(blockIndex)}
                    >
                      
                    </Button>
                  </Flex>

                  {/*  */}
                  <div
                    style={{
                      display: 'grid',
                      gridTemplateColumns: '1fr 1fr 1fr',
                      columnGap: 8,
                      rowGap: 8,
                      marginTop: 12,
                      padding: 8,
                      border: '1px dashed rgba(0,0,0,.15)',
                      borderRadius: 4
                    }}
                  >
                    {block.items.map((teacher, index) => (
                      <div
                        key={index}
                        style={{
                          display: 'block',
                          padding: 8,
                          border: '1px dashed rgba(0,0,0,.5)',
                          borderRadius: 4
                        }}
                      >
                        <div>
                          <Flex justify="flex-end" gap="8px">
                            <Button size="small" onClick={() => moveTeacher(blockIndex, index, -1)}></Button>
                            <Button size="small" onClick={() => moveTeacher(blockIndex, index, 1)}></Button>
                            <Button
                              size="small"
                              isDestructive
                              onClick={() => removeTeacher(blockIndex, index)}
                            >
                              
                            </Button>
                          </Flex>
                          <div style={{ height: 12 }} />
                          <Flex>
                            <FlexBlock>
                              <MediaUploadCheck>
                                <MediaUpload
                                  allowedTypes={['image']}
                                  value={teacher.imageId}
                                  onSelect={(media) => {
                                    updateTeacherImage(blockIndex, index, media)
                                  }}
                                  render={({ open }) => (
                                    <>
                                      {teacher.imageData?.url ? (
                                        <img
                                          src={teacher.imageData.url}
                                          style={{ width: '100%', height: 180, objectFit: 'cover', borderRadius: 6 }}
                                          onClick={open}
                                        />
                                      ) : (
                                        <Button onClick={open} variant="secondary" style={{ width: 'stretch' }}>
                                           
                                        </Button>
                                      )}
                                    </>
                                  )}
                                />
                              </MediaUploadCheck>
                            </FlexBlock>
                          </Flex>
                          <div style={{ height: 12 }} />
                          <Flex>
                            <FlexBlock>
                              <label className="my-rich-text__label"></label>
                              <RichText
                                tagName="div"
                                value={teacher.name}
                                onChange={(value) => {
                                  updateTeacherField(blockIndex, index, 'name', value)
                                }}
                                placeholder=" "
                                allowedFormats={[]}
                              />
                            </FlexBlock>
                          </Flex>
                          <Flex>
                            <FlexBlock>
                              <label className="my-rich-text__label"></label>
                              <RichText
                                tagName="div"
                                value={teacher.role}
                                onChange={(value) => {
                                  updateTeacherField(blockIndex, index, 'role', value)
                                }}
                                placeholder=" "
                                allowedFormats={[]}
                              />
                            </FlexBlock>
                          </Flex>
                          <Flex>
                            <FlexBlock>
                              <label className="my-rich-text__label"></label>
                              <RichText
                                tagName="div"
                                value={teacher.descr}
                                onChange={(value) => {
                                  updateTeacherField(blockIndex, index, 'descr', value)
                                }}
                                placeholder=" "
                                allowedFormats={[]}
                              />
                            </FlexBlock>
                          </Flex>
                        </div>
                      </div>
                    ))}
                  </div>

                  <Button
                    style={{ marginTop: 8 }}
                    variant="secondary"
                    onClick={() => addTeacher(blockIndex)}
                  >
                    +  
                  </Button>
                </div>
              ))}

              <Button variant="primary" onClick={addTeacherBlock}>
                +  
              </Button>
            </div>
          )}
        </div>
      </div>
    </>
  );
};

export default Edit;